import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import yearsofex from "../api/yearsofex";
import { editTruck, deleteTruck, findOwners, } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectTrucks extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
        showEditModal:false,
        saved:false,
        id: '',
        number : '',
        owner : 'nn',
        owners: [],
        make : '',
        vin : '',
        xtoken:'',
        msg: '',
        error: '',
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }

    handleEditNumberInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, number: v,});
    }
    handleEditOwnerInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, owner: v,});
    }
    handleEditMakeInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, make: v,});
    }
    handleEditVinInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, vin: v,});
    }
  handleEditSaveChanges = () => {
      // console.log('id is', this.state);
      editTruck(this.state.xtoken, this.state, this.state.id)
      .then((response) => {
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }
  handleClickEditRow = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    findOwners(this.props.Xtoken).then((ownerResponse) => {
        this.setState({
            ...this.state,
            owners:ownerResponse.data,
        });
    }).catch((ownerRespError) => {});
    this.setState({
        showEditModal:true,
        id: selectedRowData.id,
        number : selectedRowData.number,
        owner : selectedRowData.owner,
        make : selectedRowData.make,
        vin : selectedRowData.vin,
        xtoken:this.props.Xtoken,
    });
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteTruck(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Edit row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickEditRow}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Delete row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Edit truck
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={6} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                      <FormGroup>
                        <Label for="exampleEmail">Truck License Plate Number*</Label>
                        <Input onChange={this.handleEditNumberInput}
                          type="text" 
                          value={this.state.number}
                          name="number"
                          placeholder=""
                        />
                      </FormGroup>
                        <FormGroup>
                        <Label for="exampleEmail">Truck Owner*</Label>
                        <Input onChange={this.handleEditOwnerInput} value={this.state.owner} defaultValue={this.state.owner} type="select" name="owner">
                            <option value="nn" disabled={true}>Select owner</option>
                            {
                            this.state.owners.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{stt.company}</option>
                            ))
                            }
                        </Input>
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>

                <Col xl={6} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                      <FormGroup>
                        <Label for="exampleEmail">Truck Make*</Label>
                        <Input onChange={this.handleEditMakeInput}
                            type="text" 
                            value={this.state.make}
                            name="make"
                            placeholder="e.g TATA"
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="examplePassword">Truck VIN*</Label>
                        <Input onChange={this.handleEditVinInput}
                            type="text"
                            value={this.state.vin}
                            name="vin"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectTrucks" })(CustomToolbarSelectTrucks);